import React, { useState, useEffect } from 'react';
import { useAuth } from '../../context/AuthContext';
import { useData } from '../../context/DataContext';
import { formatDate } from '../../utils/helpers';

const Attendance = () => {
  const { user } = useAuth();
  const { addAttendance, attendanceLogs, staffList, shifts } = useData();
  const [currentShift, setCurrentShift] = useState(null);
  const [currentTime, setCurrentTime] = useState(new Date());

  // 1. Ly thng tin Ca lm vic ca User hin ti
  useEffect(() => {
    const timer = setInterval(() => setCurrentTime(new Date()), 1000);
    const currentUserData = staffList.find(s => s.id === user.id);
    if (currentUserData && currentUserData.shiftId) {
      const shift = shifts.find(s => String(s.id) === String(currentUserData.shiftId));
      setCurrentShift(shift);
    }
    return () => clearInterval(timer);
  }, [user.id, staffList, shifts]);

  const getTodayWithTime = (timeString) => {
    if (!timeString) return new Date();
    const [hours, minutes] = timeString.split(':');
    const date = new Date();
    date.setHours(parseInt(hours), parseInt(minutes), 0, 0);
    return date;
  };

  const handleCheck = (type) => {
    if (!currentShift) {
      alert("LI: Bn cha c xp ca lm vic. Vui lng lin h Admin!");
      return;
    }

    const now = new Date();
    const startTime = getTodayWithTime(currentShift.start);
    const endTime = getTodayWithTime(currentShift.end);
    let statusNote = "ng gi";

    // --- LOGIC CHECK-IN ---
    if (type === 'Check-in') {
      const diffMinutes = (now - startTime) / 60000; 
      if (diffMinutes > 3) {
        const lateMins = Math.floor(diffMinutes);
        statusNote = `Tr ${lateMins} pht`;
        alert(` THNG BO: Bn  i tr ${lateMins} pht so vi gi bt u (${currentShift.start}).\nH thng vn ghi nhn Check-in.`);
      } else {
        alert(" Check-in thnh cng! Chc bn mt ngy lm vic tt lnh.");
      }
    }

    // --- LOGIC CHECK-OUT ---
    if (type === 'Check-out') {
      const diffMinutes = (now - endTime) / 60000;
      if (diffMinutes < -10) {
        alert(` KHNG TH CHECK-OUT!\n\nCha n gi v. Bn ch c php v sm ti a 10 pht trc gi kt thc ca (${currentShift.end}).`);
        return; 
      }
      if (diffMinutes > 15) {
        alert(`  NG CNG CHECK-OUT!\n\nBn  qu hn check-out hn 15 pht so vi gi kt thc ca.\nH thng t chi ghi nhn.\nVui lng gi gii trnh cho qun l trong vng 24 gi.`);
        return; 
      }
      alert(" Check-out thnh cng! Hn gp li.");
    }

    addAttendance({
      staffName: user.name,
      staffId: user.id,
      type: type,
      shiftName: currentShift.name,
      statusNote: statusNote, 
      time: now
    });
  };

  const myHistory = attendanceLogs.filter(l => l.staffId === user.id);

  return (
    <div>
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <h2 style={{ color: '#003366' }}>im danh Nhn s</h2>
        <div style={{ fontSize: '1.5rem', fontWeight: 'bold', color: '#E67E22', background: '#fff', padding: '5px 15px', borderRadius: '5px', boxShadow: '0 2px 5px rgba(0,0,0,0.1)' }}>
          {currentTime.toLocaleTimeString('vi-VN')}
        </div>
      </div>
      
      {currentShift ? (
        <div style={{ background: '#e6f7ff', padding: '20px', borderRadius: '8px', border: '1px solid #91d5ff', marginBottom: '25px', boxShadow: '0 2px 5px rgba(0,0,0,0.05)' }}>
          <h4 style={{ margin: '0 0 10px 0', color: '#0050b3' }}>Ca lm vic hin ti: {currentShift.name}</h4>
          <div style={{ fontSize: '1.1rem', marginBottom: '10px' }}>
             Thi gian: <strong>{currentShift.start}</strong> - <strong>{currentShift.end}</strong>
          </div>
          <ul style={{ margin: 0, paddingLeft: '20px', fontSize: '0.9rem', color: '#555', fontStyle: 'italic' }}>
            <li>Check-in tr qu <strong>3 pht</strong> s b h thng ghi nhn l <strong>Tr</strong>.</li>
            <li>c php Check-out sm ti a <strong>10 pht</strong>.</li>
            <li>Cng Check-out s <strong>ng</strong> sau <strong>15 pht</strong> kt thc ca.</li>
          </ul>
        </div>
      ) : (
        <div style={{ padding: '20px', background: '#fff1f0', border: '1px solid #ffccc7', color: '#cf1322', borderRadius: '8px', marginBottom: '20px', fontWeight: 'bold', textAlign: 'center' }}>
           Bn cha c phn cng Ca lm vic. Vui lng lin h Admin.
        </div>
      )}

      <div style={{ display: 'flex', gap: '20px', marginBottom: '30px' }}>
        <button 
          onClick={() => handleCheck('Check-in')} 
          disabled={!currentShift}
          style={{ 
            padding: '20px', 
            background: !currentShift ? '#ccc' : '#28a745', 
            color: 'white', 
            border: 'none', 
            borderRadius: '8px', 
            cursor: !currentShift ? 'not-allowed' : 'pointer', 
            flex: 1, 
            fontSize: '1.2rem', 
            fontWeight: 'bold',
            boxShadow: '0 4px 0 #1e7e34',
            transition: 'transform 0.1s'
          }}
        >
          CHECK-IN (Vo ca)
        </button>
        <button 
          onClick={() => handleCheck('Check-out')}
          disabled={!currentShift}
          style={{ 
            padding: '20px', 
            background: !currentShift ? '#ccc' : '#dc3545', 
            color: 'white', 
            border: 'none', 
            borderRadius: '8px', 
            cursor: !currentShift ? 'not-allowed' : 'pointer', 
            flex: 1, 
            fontSize: '1.2rem', 
            fontWeight: 'bold',
            boxShadow: '0 4px 0 #bd2130',
            transition: 'transform 0.1s'
          }}
        >
          CHECK-OUT (Tan ca)
        </button>
      </div>

      <h3>Lch s chm cng</h3>
      <ul style={{ listStyle: 'none', padding: 0 }}>
        {myHistory.length === 0 && <p style={{ fontStyle: 'italic', color: '#999' }}>Cha c d liu im danh.</p>}
        {myHistory.slice().reverse().map((h, i) => {
          // --- SA LI  Y: Kim tra nu statusNote khng tn ti th gn chui rng ---
          const safeNote = h.statusNote || 'D liu c';
          const isLate = safeNote.includes('Tr');
          
          return (
            <li key={i} style={{ padding: '15px', borderBottom: '1px solid #eee', background: '#fff', marginBottom: '10px', borderRadius: '8px', boxShadow: '0 1px 3px rgba(0,0,0,0.05)', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <div>
                <strong style={{ color: h.type === 'Check-in' ? '#28a745' : '#dc3545' }}>{h.type}</strong>
                <div style={{ fontSize: '0.85rem', color: '#666', marginTop: '3px' }}>
                  {formatDate(h.time)} - Ca: {h.shiftName || 'N/A'}
                </div>
              </div>
              <div style={{ textAlign: 'right' }}>
                <span style={{ 
                  padding: '5px 10px', 
                  borderRadius: '15px', 
                  fontSize: '0.85rem', 
                  fontWeight: 'bold',
                  background: isLate ? '#fff1f0' : '#f6ffed',
                  color: isLate ? '#cf1322' : '#389e0d',
                  border: isLate ? '1px solid #ffccc7' : '1px solid #b7eb8f'
                }}>
                  {safeNote}
                </span>
              </div>
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default Attendance;